import { FirstSlash } from "entities/FightAbilities/dispatcher/first_slash"
import { Vampirism } from "entities/FightAbilities/dispatcher/vampirism/vampirism"
import { TFightAbilities, TFightAbilitiesKeys } from "entities/FightAbilities/types/types"

const FightAbilities: TFightAbilities = {
    first_slash: FirstSlash,
    vampirism: Vampirism,
}

export function createFightAbility(name: TFightAbilitiesKeys) {
    return FightAbilities[name]
}
